.certificate {
    padding: 20px 20px 20px;
    background-color: $grey;

    & h2 {
        @include heading-2;
    }

    & p {
        @include plain-text;
    }

    @media (min-width: $desktop-width) {
        grid-row: 5/6;
        grid-column: 1/3;
        padding: 40px 40px 20px;
    }
}